<template>
<h2>Keskustelun yhteenveto:</h2>
<div class="container">
    <div class="phaseContainer">
        <h3 @click="togglePhase('phase1')">Phase 1<span :class="{ rotatedArrow: state.phaseOneIsVisible }">></span></h3>
        <div id="phase1div" :class="{ visibleDiv: state.phaseOneIsVisible, hiddenDiv: !state.phaseOneIsVisible }">
            <div>
                <div class="grid2">
                    <h3>Kortti</h3>
                    <h3>Kategoria</h3>
                </div>
                <div class="grid2" v-for="card in state.cardArray">
                    <p>{{ card.description }}</p>
                    <p>{{ card.phase1selection }}</p>
                </div>
            </div>
        </div>
    </div>
    <div class="phaseContainer">
        <h3 @click="togglePhase('phase2')">Phase 2<span :class="{ rotatedArrow: state.phaseTwoIsVisible }">></span></h3>
        <div id="phase2div" :class="{ visibleDiv: state.phaseTwoIsVisible, hiddenDiv: !state.phaseTwoIsVisible }">
            <div>
                <div class="grid3">
                    <h3>Kortti</h3>
                    <h3>Kategoria</h3>
                    <h3>Vastaus</h3>
                </div>
                <div class="grid3" v-for="card in state.chosenPhaseTwoCards">
                    <p>{{ card.description }}</p>
                    <p>{{ card.phase2selection }}</p>
                    <p>{{ card.answer }}</p>
                </div>
            </div>
        </div>
    </div>
    <div class="phaseContainer">
        <h3 @click="togglePhase('phase3')">Phase 3<span :class="{ rotatedArrow: state.phaseThreeIsVisible }">></span></h3>
        <div id="phase3div" :class="{ visibleDiv: state.phaseThreeIsVisible, hiddenDiv: !state.phaseThreeIsVisible }">
            <div>
                <div class="grid2">
                    <h3>Kortti</h3>
                    <h3>Vastaus</h3>
                </div>
                <div class="grid2" v-for="card in state.chosenFeedbackCards">
                    <p>{{ card.description }}</p>
                    <p>{{ card.answer }}</p>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { reactive } from 'vue'

export default {
    name: 'Summary',
    setup() {

        /**
         * Summary shows the card placements with written answers in the end.
         * Phase 1 cards are shown from cardArray, Phase 2 cards are shown
         * from chosenPhaseTwoCards. They are set in right order to the array
         * based on the triangle's placement order in Phase 2.
         * Feedback cards are shown from chosenFeedbackCards array.
         */
        const cardArray = JSON.parse(localStorage.getItem('cardArray'));
        const feedbackCards = JSON.parse(localStorage.getItem('feedbackCards'));

        const chosenFeedbackCards = feedbackCards.filter(card => {
            if (card.chosen)
                return card
        });

        const chosenPhaseTwoCards = [];

        const goalCard = cardArray.find(card => card.phase2selection === 'Tavoite');
        const challengeCard = cardArray.find(card => card.phase2selection === 'Haaste');
        const assetOneCard = cardArray.find(card => card.phase2selection === 'Voimavara1');
        const assetTwoCard = cardArray.find(card => card.phase2selection === 'Voimavara2');

        chosenPhaseTwoCards.push(goalCard, challengeCard, assetOneCard, assetTwoCard);

        const state = reactive({
            cardArray: cardArray,
            chosenPhaseTwoCards: chosenPhaseTwoCards,
            chosenFeedbackCards: chosenFeedbackCards,
            phaseOneIsVisible: false,
            phaseTwoIsVisible: true,
            phaseThreeIsVisible: true
        });

        /**
         * Toggles the parameter Phase visibility value in state object.
         * Phase visibility boolean value is used for setting
         * right CSS class for the Phase's info in template.
         * Shows or hides info of the Phase.
         */
        const togglePhase = (phase) => {
            if (phase === 'phase1')
                state.phaseOneIsVisible = !state.phaseOneIsVisible
            else if (phase === 'phase2')
                state.phaseTwoIsVisible = !state.phaseTwoIsVisible
            else if (phase === 'phase3')
                state.phaseThreeIsVisible = !state.phaseThreeIsVisible
        }

        return { feedbackCards, state, togglePhase }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
    margin: 40px 0 0;
    cursor: pointer;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    margin: 30px 10px;
    display: block;
}

a {
    color: #42b983;
}

.grid2, .grid3 {
    text-align: left;
}

.grid2 {
    display: grid;
    grid-template-columns: 50% 50%;
    padding: 10px 30px;
    justify-content: center;
}

.grid2 * {
    height: 100%;
    margin: 0;
}

.grid3 {
    display: grid;
    grid-template-columns: 33.3% 33.3% 33.3%;
    padding: 10px;
}

.grid3 * {
    height: 100%;
    margin: 0;
}

h3 span {
    position: absolute;
    padding: 0 20px;
}

.rotatedArrow {
    transform: rotate(90deg);
}

.visibleDiv {
}

.hiddenDiv {
    display: none;
}

.container {
    margin: 40px 0;
    display: flex;
    justify-content: center;
    flex-direction: row;
    text-align: center;
}

.phaseContainer {
    width: 30vw;
    padding: 0 30px;
}

.phaseContainer h3 {
    margin-bottom: 10px;
}

@media only screen and (max-width: 1000px) {
    .grid3, .grid2 {
        width: 40%;
        position: relative;
        justify-content: center;
        left: 0;
        right: 0;
        margin: auto;
    }

    table {
        width: 80%;
        position: relative;
        left: 0;
        right: 0;
        margin: auto;
    }

    .container {
        margin: 40px 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        text-align: center;
    }

    .phaseContainer {
        width: 100%;
        padding: 0;
    }
}

@media only screen and (max-width: 700px) {
    .grid3, .grid2 {
        width: 85%;
        position: relative;
        justify-content: center;
        left: 0;
        right: 0;
        margin: auto;
    }
}
</style>