<template>
    <div id="answerBox">
        <!-- CardTypeSelection, CardSelection and Answer are used multiple times during Phase2 progression.
            Possible card types to choose cards from are Problem, Challenge and Wish -->
        <CardTypeSelection
            v-if="state.progress == 0"
            :title="'Valitse tavoitteen aihepiiri.'"
            @saveCardsToChooseFrom="saveCardsToChooseFrom" />
        <!-- CardSelection component is for choosing a card to Phase2 current placeholder.
            CardSelection shows cards from cardsToChooseFrom array which is saved in state
            object based on CardTypeSelection selection.
            Selection prop tells the placeholder where the card will be placed. -->
        <CardSelection
            v-if="state.progress == 1"
            :title="'Valitsit aihepiirin ' + state.cardType + '. Minkä kortin haluat käsitellä?'"
            :cardsToChooseFrom="state.cardsToChooseFrom"
            selection="Tavoite"
            backEnabled=true
            @saveCardChoice="saveCardChoice"
            @goBack="goBack"/>
            <!-- Answer component saves the answer why the card was chosen to the placeholder.
            Card and the title of Answer component is passed by props. -->
        <Answer
            v-if="state.progress == 2"
            :title="'Valitsit aihepiirin ' + state.cardType + ' ja sieltä kortin ' + state.answerToCard.description + '. Perustelethan vielä valintasi.'"
            :card="state.answerToCard"
            @answer="saveAnswer"
            @goBack="goBack"/>

        <CardTypeSelection
            v-if="state.progress == 3"
            title="Valitse haasteen aihepiiri klikkaamalla mitä tahansa aihepiiriä. Voit myös valita En osaa sanoa -kortteja."
            @saveCardsToChooseFrom="saveCardsToChooseFrom" />
        <CardSelection
            v-if="state.progress == 4"
            :title="'Valitsit aihepiirin ' + state.cardType + '. Minkä kortin haluat käsitellä? Voit myös valita En osaa sanoa -korteista.'"
            :cardsToChooseFrom="state.cardsToChooseFrom"
            selection="Haaste"
            dontKnowCardsAreActive=true
            backEnabled=true
            @saveCardChoice="saveCardChoice"
            @goBack="goBack"/>
        <Answer
            v-if="state.progress == 5"
            :title="'Valitsit aihepiirin ' + state.cardType + ' ja sieltä kortin ' + state.answerToCard.description + '. Perustelethan vielä valintasi.'"
            :card="state.answerToCard"
            @answer="saveAnswer"
            @goBack="goBack"/>

        <CardSelection
            v-if="state.progress == 6"
            :title="'Valitse ensimmäinen voimavara. Voit myös selata En osaa sanoa -kortteja.'"
            :cardsToChooseFrom="state.cardsToChooseFrom"
            dontKnowCardsAreActive=true
            selection="Voimavara1"
            @saveCardChoice="saveCardChoice"
            @goBack="goBack"/>
        <Answer
            v-if="state.progress == 7"
            :title="'Valitsit ensimmäiseksi voimavaraksi kortin ' + state.answerToCard.description + '. Perustelethan vielä valintasi.'"
            :card="state.answerToCard"
            @answer="saveAnswer"
            @goBack="goBack"/>

        <CardSelection
            v-if="state.progress == 8"
            :title="'Valitse toinen voimavara. Voit myös selata En osaa sanoa -kortteja.'"
            :cardsToChooseFrom="state.cardsToChooseFrom"
            dontKnowCardsAreActive=true
            selection="Voimavara2"
            @saveCardChoice="saveCardChoice"
            @goBack="goBack"/>
        <Answer
            v-if="state.progress == 9"
            :title="'Valitsit toiseksi voimavaraksi kortin ' + state.answerToCard.description + '. Perustelethan vielä valintasi.'"
            :card="state.answerToCard"
            @answer="saveAnswer"
            @goBack="goBack"/>
    </div>
</template>

<script>

import {reactive} from "vue";
import Card from "./Card.vue";
import Answer from "./Answer.vue";
import CardTypeSelection from './CardTypeSelection.vue';
import CardSelection from './CardSelection.vue';

export default {
    name: "Overlay",
    components: {
        Card,
        Answer,
        CardTypeSelection,
        CardSelection
    },
    setup(props, {emit}) {

        /**
         * State object saves the progress index of the Overlay. The right component is
         * shown to the user based on the progress index.
         * cardsToChooseFrom is passed as a prop to CardSelection component when it's
         * time to choose a card for the placeholder.
         * answerToCard is passed to Answer component to let it know which card the
         * answer will be saved to.
         * Titles array saves the titles for different progress parts of the Overlay.
         * First title for progress 0, second for progress 1 and so on...
         * history array saves the actions user has taken during the overlay so when goBack
         * is called, the right kind of information is deleted from right card based on the history. 
         */
        const state = reactive({
            progress: 0,
            cardsToChooseFrom: [],
            cardType: null,
            answerToCard: null,
            history: [],
        });

        const saveCardsToChooseFrom = (cardsToChooseFrom, type) => {

            /**
             * Save cardsToChooseFrom array to state from parameter.
             * Save the history object for later use, if goBack will be selected.
             */
            console.log(cardsToChooseFrom, type);
            state.cardsToChooseFrom = cardsToChooseFrom;
            state.cardType = type;
            state.history[state.progress] = { cardsToChooseFrom: cardsToChooseFrom }
            state.progress++;
            console.log(state.history)
        }

        const saveCardChoice = (card, selection) => {

            /**
             * Gets the cardArray from localStorage, finds the right card by id of the card in parameter.
             * Saves the phase2selection to the card and saves the array back to localStorage.
             */
            const cardArray = JSON.parse(localStorage.getItem("cardArray"));
            const cardToModifyIndex = cardArray.findIndex(cardInArray => cardInArray.id === card.id);

            cardArray[cardToModifyIndex].phase2selection = selection;
            localStorage.setItem("cardArray", JSON.stringify(cardArray));

            /**
             * Saves the current card to answerToCard so it can be passed to
             * Answer component.
             * Saves the modified cards index number in cardArray and property which was modified
             * to be used by goBack.
             */
            state.answerToCard = card
            state.history[state.progress] = { modifiedCardIndex: cardToModifyIndex, modifiedProperty: "phase2selection" }
            state.progress++
            console.log(state.history)
        }

        const saveAnswer = (card, answer) => {

            /**
             * Gets the cardArray from localStorage, finds the right card by id of the card in parameter.
             * Saves the answer to the card and saves the array back to localStorage.
             */
            const cardArray = JSON.parse(localStorage.getItem("cardArray"));
            const cardToModifyIndex = cardArray.findIndex(cardInArray => cardInArray.id === card.id);
            cardArray[cardToModifyIndex].answer = answer;
            localStorage.setItem("cardArray", JSON.stringify(cardArray));

            state.answerToCard = null;

            /**
             * Saves the modified cards index number in cardArray and property which was modified
             * to be used by goBack.
             */
            state.history[state.progress] = { modifiedCardIndex: cardToModifyIndex, modifiedProperty: "answer" }

            state.progress++;

            /**
             * If the next stage after Answer component in progress will be Asset1 or Asset2
             * sets the cardsToChooseFrom array to contain Asset cards to be passed to
             * CardSelection component
             */
            if (state.progress === 6)
                state.cardsToChooseFrom = cardArray.filter(card => {
                    if (card.phase1selection === "Voimavara" && !card.phase2selection)
                        return card
                })

            if (state.progress === 8)
                state.cardsToChooseFrom = cardArray.filter(card => {
                    if (card.phase1selection === "Voimavara" && !card.phase2selection)
                        return card
                })
            console.log(state.history)
            if (state.progress === 10)
                emit("phasecomplete")

            emit("deactivateOverlay", state.progress);
        }

        const goBack = () => {
            state.progress--

            /**
             * Checks if history arrays current (based on the progress number) object has
             * cardsToChooseFrom or modifiedCardIndex property.
             * Set state.cardsToChooseFrom array to the one in history or deletes
             * card's property based on the modifiedProperty. 
             */
            if (state.history[state.progress].cardsToChooseFrom)
                state.cardsToChooseFrom = state.history[state.progress].cardsToChooseFrom
            else if (typeof state.history[state.progress].modifiedCardIndex !== 'undefined') {
                const cardArray = JSON.parse(localStorage.getItem("cardArray"))
                if (state.history[state.progress].modifiedProperty === "phase2selection") {
                    delete cardArray[state.history[state.progress].modifiedCardIndex].phase2selection
                    localStorage.setItem("cardArray", JSON.stringify(cardArray))
                }
                else if (state.history[state.progress].modifiedProperty === "answer") {
                    delete cardArray[state.history[state.progress].modifiedCardIndex].answer
                    localStorage.setItem("cardArray", JSON.stringify(cardArray))
                }}
        }

        return { state, saveAnswer, saveCardsToChooseFrom, goBack, saveCardChoice };
    },
    emits: ['phasecomplete', 'deactivateOverlay'],
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h2 {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 20px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

#answerBox {
    border-style: solid;
    border-width: 0px;
    border-color: rgb(100, 100, 100);
    position: absolute;
    background-color: rgba(120, 120, 120, 0.5);
    padding: 250px 0;
    border-radius: 3px;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}

#answerBox::before {
    content: '';
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    backdrop-filter: blur(3px);
    z-index: -1;
}

#textBox {
    border-style: solid;
    border-width: 1px;
    border-color: rgb(150, 150, 150);
    display: inline-block;
    resize: none;
}

button {
    background-color: rgb(255, 255, 170);
    border-style: none;
    font-size: 17px;
    width: 170px;
    height: 30px;
}

#backButton {
    display: inline-block;
    position: relative;
    left: -200px;
}

#answerButton {
    display: inline-block;
    position: relative;
    left: 200px;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-size: 40px;
    color: rgb(180, 180, 180);
    text-align: center;
    transform: translate3d(0, 115px,0);
    opacity: 1; /* Firefox */
}

</style>
