<template>
    <div class="container">
        <h2>{{ title }}</h2>
        <textarea id="textBox" name="textBox" v-model="comment" placeholder="KIRJOITA TÄHÄN"></textarea>
        <div>
            <button id="backButton" @click="goBack">Takaisin</button>
            <button id="answerButton" @click="saveAnswer">Kirjaa vastauksesi</button>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue';

export default {
    name: 'Answer',
    props: {
        title: String,
        card: Object,
    },
    setup(props, {emit}) {

        const comment = ref('');

        const goBack = () => {
            emit("goBack");
        }

        const saveAnswer = () => {
            emit("answer", props.card, comment.value);
        }

        return { goBack, saveAnswer, comment }
    },
    emits: ['goBack', 'answer'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html, body, #answerBox {
    margin: 0px;
    padding: 0px;
}

h2 {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 20px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

#textBox {
    font-size: 20px;
    border: 2px solid #000;
    border-radius: 3px;
    margin: 10px 0;
    padding: 10px;
    width: 45%;
    height: 150px;
    resize: none;
}

button {
    background-color: rgb(255, 255, 170);
    border-style: none;
    font-size: 17px;
    margin: 0 10px;
}

#backButton {
    display: inline-block;
    position: relative;
}

#answerButton {
    display: inline-block;
    position: relative;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-size: 20px;
    color: rgb(180, 180, 180);
    text-transform: capitalize;
    opacity: 1; /* Firefox */
}

</style>
