<template>
    <Navbar class="nav"/>
    <Phase1 @phasecomplete="changePhase" v-if="state.phase==1"/>
    <Phase2 @phasecomplete="changePhase" v-if="state.phase==2"/>
    <Phase3 @restartFromBeginning="restartFromBeginning" @restartFromPhaseTwo="restartFromPhaseTwo" v-if="state.phase==3"/>
</template>

<script>
import Navbar from '../components/Navbar.vue';
import Phase1 from '../components/Phase1.vue';
import Phase2 from '../components/Phase2.vue';
import Phase3 from '../components/Phase3.vue';
import {reactive} from 'vue';

export default {
    name: 'Conversation',
    components:{
        Phase1,
        Phase2,
        Phase3,
        Navbar
    },
    setup() {

        /**
         * When a Phase component emits "phasecomplete", changePhase function is called
         * and a state.phase is increased. It's a reactive constant so the template
         * gets the information immediately.
         */
        const state = reactive({ phase: 1 });
        const changePhase = () => { state.phase++ };

        const restartFromBeginning = () => {
            state.phase = 1
        }

        const restartFromPhaseTwo = () => {
            state.phase = 2
        }

        return { state, changePhase, restartFromBeginning, restartFromPhaseTwo };
    }
}
</script>

<style>

h3 {
  margin: 40px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

/*
  Button animation which is used mostly in conversation.
  Fades to slithly darker yellow in 450 millisecond.
*/
button {
    transition: background-color 350ms ease-in-out;
}

button:hover, .btn:active {
    background-color: #d9dc6f;
}
</style>
