<template>
    <div v-if="!state.backToStart" class="container">
        <div :class="{
                visible: state.triangleIsVisible,
                hidden: !state.triangleIsVisible,
                fadeIn: state.triangleFadeIn,
                fadeOut: state.triangleFadeOut,
                mouseActive: !state.haltProgram,
                mouseDisabled: state.haltProgram,
            }" id="trigendaBoard">
            <div @click="saveSelection('Voimavara')"></div>
            <div @click="saveSelection('Haaste')"></div>
            <div @click="saveSelection('Toive')"></div>
            <div @click="saveSelection('Ongelma')"></div>
        </div>
    </div>
    <div :class="{
            visible: state.cardIsVisible,
            hidden: !state.cardIsVisible,
            fadeIn: state.cardFadeIn,
            fadeOut: state.cardFadeOut,
            mouseActive: !state.haltProgram,
            mouseDisabled: state.haltProgram,
        }" v-if="!state.backToStart" class="container">
        <button @click="saveSelection('EnOsaaSanoa')" class="btn" id="next">En osaa sanoa</button>
    </div>
    <div :class="{
            visible: state.cardIsVisible,
            hidden: !state.cardIsVisible,
            fadeIn: state.cardFadeIn,
            fadeOut: state.cardFadeOut,
            mouseActive: !state.haltProgram,
            mouseDisabled: state.haltProgram,
        }" v-if="!state.backToStart" id="cardElement">
        <Card :description="state.currentCard.description"/>
    </div>
    <p :class="{
            visible: state.cardIsVisible,
            hidden: !state.cardIsVisible,
            fadeIn: state.cardFadeIn,
            fadeOut: state.cardFadeOut,
            mouseActive: !state.haltProgram,
            mouseDisabled: state.haltProgram,
        }" v-if="!state.backToStart">Kortteja nostettu {{ state.cardsDrawn + 1 }} / {{ decks[1].cards.length }}</p>
    <div v-if="state.backToStart">
        <p>Valitse kategorioihin <b>HAASTE</b>, <b>TOIVE</b> ja <b>ONGELMA</b> yhteensä vähintään 2 korttia
        ja kategoriaan <b>VOIMAVARA</b> vähintään 2 korttia, jotta saamme keskustelusta mahdollisimman
        kattavan.</p>
        <button class="btn" @click="startOver">Takaisin alkuun</button>
    </div>
    <Tip :class="{
            visible: state.tipIsVisible,
            hidden: !state.tipIsVisible,
            fadeIn: state.tipFadeIn,
            fadeOut: state.tipFadeOut,
            mouseActive: !state.haltProgram,
            mouseDisabled: state.haltProgram,
        }" v-if="!state.backToStart" :tipMsg="state.tipMsg[state.tipIndex]" :mood="state.tipMood" />
    <div class="container">
        <div :class="{
                visible: state.helpTextIsVisible,
                hidden: !state.helpTextIsVisible,
                fadeIn: state.helpTextFadeIn,
                fadeOut: state.helpTextFadeOut,
                mouseActive: !state.haltProgram,
                mouseDisabled: state.haltProgram,
            }" v-if="!state.backToStart" id="helpText">
            <p><span id="assetText">Voimavara: </span>Koet tämän asian vahvuutenasi, saat siitä voimaa ja se auttaa jaksamaan.</p>
            <p><span id="wishText">Toive: </span>Tämä asia ei välttämättä toteudu täysin, mutta toivoisit sen toteutuvan.</p>
            <p><span id="challengeText">Haaste: </span>Hidastaa ja vaikeuttaa tavoitteiden toteutumista.</p>
            <p><span id="problemText">Ongelma: </span>Pysäyttää ja estää tavoitteita.</p>
        </div>
    </div>
</template>

<script>
import Card from "./Card.vue";
import decks from "../decks.json";
import Tip from '../components/Tip.vue'
import {reactive} from "vue";

export default {
    name: 'Phase1',
    components: {
        Card,
        Tip
    },
    setup(props, {emit}) {
        localStorage.removeItem("cardArray");

        /**
         * Constant state is made a reactive object, which means when you change it's
         * properties, the changes show reactively in the template as well.
         *
         * cardsDrawn tells how many cards have the user gone through
         * currentCard tells the description of the currenlty shown card.
         *
         * if backToStart is true, the message to choose at least total of 2 cards in
         * Challenge, Wish and Problem cards and total of 2 cards in Asset cards is shown at
         * the end of Phase 1.
         *
         * There are also variables for conditional CSS visibility of the elements and haltProgram
         * which sets conditional CSS to prevent mouse events.
         *
         * tipMsg and tipMood are passed as props to Tip component to show a message and set
         * Tringo's mood. Mood can be 'happy', 'confused' or 'sad'.
         * tipIndex tells which message will be passed to Tip component from tipMsg array.
         */
        const state = reactive({
            cardsDrawn: 0,
            currentCard: decks[1].cards[0],
            haltProgram: true,
            triangleIsVisible: false,
            triangleFadeIn: false,
            triangleFadeOut: false,
            helpTextIsVisible: false,
            helpTextFadeIn: false,
            helpTextFadeOut: false,
            tipIsVisible: false,
            tipFadeIn: false,
            tipFadeOut: false,
            cardIsVisible: false,
            cardFadeIn: false,
            cardFadeOut: false,
            backToStart: false,
            tipMsg: [
                ['Hei! Olet aloittanut Trigenda-Keskustelun.', 'Tämä on keskustelun ensimmäinen vaihe. Näytän sinulle nyt kortteja yksitellen. Sinun tehtäväsi on valita, mihin lokeroon asetat kunkin kortin.'],
                ['Pyrithän valitsemaan vähintään kaksi voimavaraa, yhden ongelman ja yhden haasteen.', 'Jos kortti ei mielestäsi sovellu mihinkään lokeroon, voit valita "en osaa sanoa".']
            ],
            tipIndex: 0,
            tipMood: 'happy',
        });

        const fadeInElements = () => {
            setTimeout(() => {
                state.triangleIsVisible = true;
                state.triangleFadeIn = true;
                setTimeout(() => {
                    state.helpTextIsVisible = true;
                    state.helpTextFadeIn = true;
                    setTimeout(() => {
                        state.tipIsVisible = true;
                        state.tipFadeIn = true;
                        setTimeout(() => {
                            state.cardIsVisible = true;
                            state.cardFadeIn = true;
                            setTimeout(() => {
                                state.haltProgram = false;
                            }, 1500)
                        }, 1500)
                    }, 1500)
                }, 1500)
            }, 500)
        }

        const fadeOutElements = () => {
            state.haltProgram = true;
            state.triangleFadeOut = true;
            state.helpTextFadeOut = true;
            state.tipFadeOut = true;
            state.cardFadeOut = true;
            setTimeout(() => {
                emit("phasecomplete");
            }, 1000)
        }
        fadeInElements();

        /**
         * Function saveSelection saves the card index number in the deck.cards array
         * and where the user placed the card in the browser's localStorage.
         * This will be changed later to save the selection to a database.
         *
         * Each template card placing button calls saveSelection with different
         * selection parameter which tells where the card was placed. 
         */
        const saveSelection = (selection) => {
            if (state.tipIndex < 1)
                state.tipIndex++

            /**
             * If there are still cards which haven't been saved, save the current
             * card's selection, else emit 'phasecomplete' to the parent component
             * to get to the next phase
             */

            const saveData = {...state.currentCard, phase1selection: selection};
            let cardArray = [];

            if (localStorage.getItem("cardArray"))
                cardArray = JSON.parse(localStorage.getItem("cardArray"));

            cardArray.push(saveData);
            localStorage.setItem("cardArray", JSON.stringify(cardArray));


            /**
             * When there are no cards left, check if there are total of 2 cards
             * in Challenge, Wish and Problem cards and total of 2 cards in
             * Asser cards. If so, continue to Phase 2, otherwise start over Phase 1.
             */
            if (state.cardsDrawn == decks[1].cards.length - 1) {
                const phaseSuccessful = checkIfPhaseSuccessful();
                if (phaseSuccessful) {
                    fadeOutElements();
                    return;
                } else
                    state.backToStart = true;
            }

            state.cardsDrawn++;
            state.currentCard = decks[1].cards[state.cardsDrawn];

        }

        const checkIfPhaseSuccessful = () => {

            let challengeWishProblemCount = 0;
            let assetCount = 0;
            const cardArray = JSON.parse(localStorage.getItem('cardArray'));

            /**
             * Count total cards in Challenge, Wish and Problem cards.
             * Count total cards in Asset cards.
             */
            cardArray.forEach(card => {
                if (card.phase1selection === 'Haaste' ||
                    card.phase1selection === 'Toive' ||
                    card.phase1selection === 'Ongelma')
                    challengeWishProblemCount++;
                else if (card.phase1selection === 'Voimavara')
                    assetCount++;
            });

            if (challengeWishProblemCount < 2 || assetCount < 2)
                return false;
            else
                return true;
        }

        const startOver = () => {
            localStorage.removeItem("cardArray");
            state.cardsDrawn = 0;
            state.currentCard = decks[1].cards[0];
            state.backToStart = false;
            state.tipIndex = 0;
        }

        return { decks, state, saveSelection, startOver, checkIfPhaseSuccessful, fadeOutElements };
    },
    emits: ['phasecomplete'],
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
h3 {
    margin: 40px 0 0;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.container:nth-of-type(1) {
    margin-top: 6em;
    margin-bottom: 2em;
}

.container:nth-of-type(2) {
    height: 40px;
}

#trigendaBoard {
    position: relative;
    align-items: start;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(6, 1fr);
    grid-row-gap: 0px;
    grid-column-gap: 0px;
    min-width: 400px;
    max-width: 400px;
    justify-content: center;
}

#trigendaBoard div, #next {
    cursor: pointer;
}

#trigendaBoard div {
    padding: 5px;
    position: relative;
    text-transform: uppercase;
}

#trigendaBoard div:nth-of-type(1) {
    background-color: #60DAE2;
    grid-column: 2 / 3;
    grid-row: 1;
}

#trigendaBoard div:nth-of-type(1)::before {
    content: 'Voimavara';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div:nth-of-type(2) {
    background-color: #F8EA5A;
    grid-column: 1 / 2;
    grid-row: 2;
    left: 110px;
}

#trigendaBoard div:nth-of-type(2)::before {
    content: 'Haaste';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div:nth-of-type(3) {
    background-color: #7FFB6E;
    grid-column: 2 / 3;
    grid-row: 2;
    transform: rotate(180deg);
    right: 10px;
    top: -20px;
}

#trigendaBoard div:nth-of-type(3)::before {
    content: 'Toive';
    position: absolute;
    top: 15px;
    right: 10px;
    bottom: 0;
    left: 0;
    height: 100px;
    transform: rotate(180deg);
}

#trigendaBoard div:nth-of-type(4) {
    background-color: #FEA35F;
    grid-column: 3 / 3;
    grid-row: 2;
    right: 110px;
}

#trigendaBoard div:nth-of-type(4)::before {
    content: 'Ongelma';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div {
    /*background-color: rgba(0,0,0,0.1);*/
    margin: 5px;
    height: 170px;
    width: 12em;
    clip-path: polygon(192px 170px, 96px 0, 0px 170px);
    border-radius: 2px;
}

#trigendaBoard div {
    transition: background-color 250ms ease-in-out;
}

#trigendaBoard div:hover, #trigendaBoard div:active {
    background-color: pink;
}

#cardElement {
    position: relative;
}

.visible {
    visibility: visible;
}

.hidden {
    visibility: hidden;
}

.fadeIn {
    animation: fadeInAnimation ease 1s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.fadeOut {
    animation: fadeOutAnimation ease 1s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.mouseActive {
    pointer-events: auto;
}

.mouseDisabled {
    pointer-events: none;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}

@keyframes fadeOutAnimation {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
     }
}

button {
    background-color: rgb(255, 255, 170);
    border-style: none;
    font-size: 17px;
    padding: 10px 25px;
    display: inline-block;
    position: absolute;
    text-align: center;
}

#helpText {
    text-align: left;
    position: absolute;
    top: 7em;
    right: 5em;
    width: 21em;
}

#assetText {
    color: #2FBFC8;
}

#wishText {
    color: #55BA47;
}

#challengeText {
    color: #D4C629;
}

#problemText {
    color: #F57B20;
}

@media only screen and (max-width: 500px) {
    #trigendaBoard div {
        transform: scale(0.9);
    }

    #trigendaBoard div:nth-child(1) {
        top: 15px;
        right: 1px;
    }

    #trigendaBoard div:nth-child(2) {
        left: 118px;
    }

    #trigendaBoard div:nth-child(3) {
        transform: scale(0.9) rotate(180deg);
    }

    #trigendaBoard div:nth-child(4) {
        right: 121px;
    }

    #trigendaBoard {
        padding-left: 10px;
    }
}

@media only screen and (max-width: 1200px) {
    #helpText {
        visibility: hidden;
    }
}

</style>
