import { createWebHistory, createRouter } from "vue-router";
import Main from "../sites/Main.vue";
import Conversation from "../sites/Conversation.vue";

import Phase1 from "../components/Phase1.vue"
import Phase2 from "../components/Phase2.vue"
import TrigendaInfo from "../components/TrigendaInfo.vue"
import ContactInfo from "../components/ContactInfo.vue"

const routes = [
    {
        path: "/",
        name: "Main",
        component: Main,
    },
    {
        path: "/conversation",
        name: "Conversation",
        component: Conversation,
    },
    {
        path: "/phase1",
        name: "Phase1",
        component: Phase1,
    },
    {
        path: "/phase2",
        name: "Phase2",
        component: Phase2,
    },
    {
        path: "/trigendainfo",
        name: "TrigendaInfo",
        component: TrigendaInfo,
    },
    {
        path: "/contactinfo",
        name: "ContactInfo",
        component: ContactInfo
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;